@import '../../../../style/_variables.scss';
@import '../../../../style/_mixins.scss';
.testimonials{
    @include responsiveSection;
    background: $light;
    padding: 100px;
    display: flex;
    flex-flow: column;

    @media(max-width: 900px){
        padding: $gutterOne;
        padding-top: 0;
    }
    @media(max-width: 500px){
        padding: $gutterTwo;
    }
    h1{
        margin-bottom:20px 
    }

    h1::after{
        @include afterTitle;
    }

    p{
    
        @media(max-width: 900px){
            padding: 0;
        }
    }
   .quoteLeft{
       opacity: .05;
       transform: translate(-30px, 30px);
   }

 .name{
     font-size: 14px;
 }
 .slide{
    padding: 10px;
}  
.slides{
 
}
}
.person{
    font-weight: bold;
    font-size: 14px;
    padding-top: 10px;
    
    span{
        opacity: .6;
    }
}


.navigation{
    display: flex;
    justify-content: center;
    button{
        background: $light;
        border: none;
    }
}
