@import '../../style/_variables.scss';

.logoContainer{
    display: flex;
    justify-content: center;
    width: 100%;
    background: $primary-dark;
    height: 100px;
    position: fixed;
    top:0;
    z-index: 5000;
    display:none;
    @media(max-width: 1260px){
        display: initial;
    }
 
}
.logo{
    display: flex;
    flex-flow: column;
    align-items: center;
    font-weight: 300;
    line-height: 1.2;
    font-family: 'Prata';
   
    font-size: 19px;
    padding: 20px;
  
    h4{
        color: $secondary;
        font-size: 21px;
        font-weight: bold;
    }
    color: white;
    
}