@import '../../../../style/_variables.scss';
@import '../../../../style/mixins.scss';
.hero{
    @include responsiveSection;
    background: linear-gradient(#2c2c2c73, #2c2c2c80), url('../../../../images/houseTwo.jpg');
    background-size: cover;
    min-height: 80vh;
    background-position: center;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    img{
        height: 400px;
        transform:translateX(-100px);
    }
    @media(max-width: 500px){
        min-height: 90vh;
        h1{
            font-size: 18px;
        }
    }
}


.block{
    background:$light;
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 50px 110px;
    @media(max-width: 900px){
        background:$primary-dark;
        padding: 40px 30px;
        h1{
            color:$secondary;
        }
    }
    
    button{
        border:2px solid $primary-dark;
        margin-left: 5px;
        padding: 10px;
        cursor: pointer;
        color: $text-dark;
        font-weight: bold;
        background: $light;
        font-size: 14px;
       
    }
}